@use "sass:math";

////////////////////
// 
////////////////////
// 
$inner: 1050px;
// 

// 
$padding-pc: 25px;
$padding-sp: 20px;
// PCPC25px


////////////////////
// 
////////////////////

// $ja-base-font-family: 'Noto Serif JP', serif;
$ja-base-font-family: 'Noto Serif JP', serif;
$ja-second-font-family: "", YuGothic, " Medium", "Yu Gothic Medium", "", "Yu Gothic", sans-serif;
$en-base-font-family: ' ProN','Hiragino Mincho ProN',sans-serif;
$en-second-font-family: 'Helvetica',sans-serif;
// 

body {
  font-family: $ja-base-font-family;
}

// 
$regular: 300;
$normal: 400;
$medium: 500;
$semi-bold: 600;
$bold: 700;

////////////////////
// 
////////////////////

//
$black: #111;
$white:  #F1F0EF;
$white2: #fff;
$main-blue: #1d425c;
$light-blue: #1c3f57;
$bg-blue: #062439;
$beige: #f1f0ef;
$gray: #C4C4C4;



////////////////////
// 
////////////////////

//
$breakpoints: (
  sm: 600,
  md: 768,
  lg: 1024,
  xl: 1440,
);


//
$mediaquerys: (
  sm: "screen and (min-width: #{map-get($breakpoints,'sm')}px)",
  md: "screen and (min-width: #{map-get($breakpoints,'md')}px)",
  lg: "screen and (min-width: #{map-get($breakpoints,'lg')}px)",
  xl: "screen and (min-width: #{map-get($breakpoints,'xl')}px)",
);

// @include mq(){}md
@mixin mq($mediaquery: md) {
  @media #{map-get($mediaquerys, $mediaquery)} {
    @content;
  }
}

// rem16px10px1610
@function rem($pixels) {
  @return math.div($pixels, 16) * 1rem;
}


////////////////////
// 
////////////////////

//768px
.u-desktop {
  display: none;
  @include mq("md") {
    display: block;
  }
}

//
.u-mobile {
  @include mq("md") {
    display: none;
  }
}
